@import url('https://fonts.googleapis.com/css2?family=Spartan:wght@600&display=swap');

* {
  margin: 0;
  padding: 0;
  font-family: 'Spartan', sans-serif;
  }

.App {
  text-align: left;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.subpageWrapper {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 3rem;
  min-height: 0;
}

.portfolioWrapper{
  padding: 3rem;
  min-height: 0;
}

.gifs{
  display:flex;
  justify-content: space-between;
}

.projects{
  text-align: center;
  display: grid;
  gap: 4rem;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
}

.projects > a {
  background: #34524f;
  padding: 1rem;
  display: grid;
  text-decoration: none;
}

.projects > a::before{
  content: "";
  padding-bottom: 100%;
  display: block;
  grid-area: 1 / 1 / 2 / 2;
}

.projects > a > img, 
.projects > a > p, 
.projects > a > p:visited,
.projects > a > p:link  {
  width: 100%;
  height: 100%;
  display:flex;
  object-fit: contain;
  grid-area: 1 / 1 / 2 / 2;
  justify-content: center;
  align-items: center;
  color: white;
}

.socialLink, .socialLink:visited {
  font-size: 40px;
  color: black;
}

.socialLink:hover{
  color: #7BABA6;
}

.projectLinks:hover,.otherLinks:hover {
  background-color:#7BABA6;
}

.panel{
  display: flex;
}

/* Control the left side */
.left {
  left: 0;
  display: flex;
  width: 50%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow-x: hidden;
  height: 100vh;
  min-height: 0;
}

/* Control the right side */
.right {
  right: 0;
  background: #34524f;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 50%;
  height: 100vh;
  overflow-x: hidden;
}
.myPic{
  width: 300px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  border-radius: 5px;
  align-self: center;
}

/*Media Queries*/

@media screen and (max-width:1700px) {
  .panel{
    height: auto;
    display: flex;
    justify-content: center;
    min-height: 0;
  }
  .left{
    width: 50%;
    padding-bottom: 1rem;
    padding-top: 1rem;
    min-height: 0;
  }

  .gifs{
    height: 240px;
    flex-direction: column;
    align-items: center;
  }
}
@media screen and (max-width:768px) {
  .panel{
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    min-height: 0;
  }
  
  .right {
    width:100%;
    height: 100%;
    overflow-x: visible;
  }
  .left{
    width: 100%;
    height: 85vh;
    margin-bottom: 1rem;
    padding-top: 1rem;
    min-height: 0;
  }

  .gifs{
    height: 240px;
    flex-direction: column;
    align-items: center;
  }
}